}

/**
 * Clickable labels
 */
label[for] {
  cursor: pointer;
}

/* Interactive */
/* ============================================ */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/*
 * Remove outline for editable content.
 */
[contenteditable]:focus {
  outline: auto;
}

/* Tables */
/* ============================================ */
/**
1. Correct table border color inheritance in all Chrome and Safari.
*/
table {
  border-color: inherit; /* 1 */
  border-collapse: collapse;
}

caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  background-color: var(--base-color);
  height: 5rem;
}
.header__body {
  padding: 1.25rem clamp(1rem, 0.6538461538rem + 1.4769230769vw, 2.5rem);
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  width: 18.75rem;
  height: 50rem;
  background-color: var(--main-color);
  transform: translateX(-300px);
  transition: transform 0.3s;
}
.nav__list {
  padding: 3.75rem 1.5625rem 0 1.5625rem;
}
.nav__item {
  padding: 1.25rem 0;
  width: 100%;
  border-top: 0.0625rem solid var(--base-color);
  border-bottom: 0.0625rem solid var(--base-color);
}
.nav__item:not(:first-of-type) {
  border-bottom: 0.0625rem solid var(--base-color);
}
.nav__link {
  font-size: 0.9rem;
  color: var(--base-color);
}
.nav__sns-list {
  padding: 4.0625rem 0 0 1.5625rem;
}
.nav__sns-item:not(:first-of-type) {
  margin-top: 0.625rem;
}
.nav__sns-link {
  font-size: 0.9rem;
  color: var(--base-color);
}

.nav.is-active {
  transform: none;
}

.hamburger-menu {
  position: relative;
  z-index: 10;
  width: 2.5rem;
  height: 3.125rem;
}

.hamburger-menu__line {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.875rem;
  height: 0.125rem;
  background-color: var(--main-color);
  transition: 0.3s;
}

.hamburger-menu__line:nth-child(1) {
  top: calc(50% - 8px);
}

.hamburger-menu__line:nth-child(3) {
  top: calc(50% + 8px);
}

.hamburger-menu.is-active > .hamburger-menu__line {
  background-color: var(--base-color);
}

.hamburger-menu.is-active > .hamburger-menu__line:nth-last-child(1) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-315deg);
}

.hamburger-menu.is-active > .hamburger-menu__line:nth-last-child(2) {
  opacity: 0;
}

.hamburger-menu.is-active > .hamburger-menu__line:nth-last-child(3) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(315deg);
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 50rem;
  background-color: var(--mask-color);
  opacity: 0;
  visibility: hidden;
  transform: opacity 0.3s;
}

.mask.is-active {
  opacity: 1;
  visibility: visible;
}

.container {
  max-width: 75.25rem;
  margin: 0 auto;
  padding: 0 1rem;
}

.footer {
  background-color: var(--main-color);
}
.footer__copy-body {
  padding: px-to-rem(10px) 0;
  text-align: center;
}
.footer__copy {
  color: var(--base-color);
}

.btn {
  display: inline-block;
  text-align: center;
  width: 100%;
  padding: 0.75rem 0;
}
@media (min-width: 992px) {
  .btn {
    width: 12.5rem;
  }
}

.btn--submit {
  border: 0.0625rem solid var(--base-color);
  font-size: 0.9rem;
  line-height: 1.7;
  color: var(--base-color);
  transition: 0.3s;
}

.btn--submit:hover {
  background-color: var(--base-color);
  color: var(--main-color);
}

.section-title {
  font-size: clamp(1.5rem, 1.3269230769rem + 0.7384615385vw, 2.25rem);
  font-weight: bold;
  line-height: 1.7;
  text-align: center;
}

.swiper-container {
  margin-top: clamp(1.25rem, 1.1057692308rem + 0.6153846154vw, 1.875rem);
}

.swiper {
  max-width: 100%; /* 最大幅を維持 */
  max-height: 16.9375rem; /* 高さは画像に合わせる */
}

.swiper-slide img {
  width: auto;
  height: 100%;
  object-fit: contain; /* 画像を歪まず等倍表示 */
  display: block;
  margin: auto;
}

.card-body {
  margin-top: clamp(1.25rem, 1.1057692308rem + 0.6153846154vw, 1.875rem);
}

.card-wrapper {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 1.625rem;
}
@media (min-width: 768px) {
  .card-wrapper {
    column-gap: 1.6875rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1100px) {
  .card-wrapper {
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  background-color: var(--base-color);
  box-shadow: 0px 0px 0.5rem 0.25rem var(--DropShadow-color);
}
.card__image {
  width: 100%;
  height: auto;
  object-fit: cover;
}
.card__image img {
  width: 100%;
  height: 100%;
}
.card__desc {
  padding: clamp(1rem, 0.7980769231rem + 0.8615384615vw, 1.875rem);
}
.card__text-body {
  margin: 1.25rem 0;
}
.card__text {
  font-size: 0.9rem;
}
.card__label, .card__time {
  font-size: 0.75rem;
}
.card__time {
  display: block;
  text-align: right;
}

.fadeIn {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.5s;
}

.fadeIn.fade-in-active {
  opacity: 1;
  transform: none;
}

.mv {
  margin-top: 5rem;
}
.mv__video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}
@media (min-width: 992px) {
  .mv__video {
    max-height: 60rem;
  }
}

.pickup {
  margin-top: clamp(5rem, 4.7115384615rem + 1.2307692308vw, 6.25rem);
}

.feature {
  margin-top: 6.25rem;
}

.contact {
  margin-top: 5.625rem;
  padding: 5rem 0;
  background-color: var(--main-color);
}
@media (min-width: 992px) {
  .contact {
    margin-top: 3.75rem;
    padding: 5rem 0;
  }
}
.contact__title {
  color: var(--base-color);
}

.form-body {
  margin-top: clamp(1.25rem, 1.1057692308rem + 0.6153846154vw, 1.875rem);
}

.form-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column-reverse;
}
@media (min-width: 992px) {
  .form-wrapper {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}

.form {
  margin-top: 1.875rem;
}
@media (min-width: 992px) {
  .form {
    margin-top: 0;
    width: 50%;
  }
}
.form__item {
  margin-bottom: 1.25rem;
}
.form__title {
  color: var(--base-color);
  font-size: 0.9rem;
  line-height: 1.7;
}

input[type=text],
input[type=email],
textarea {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  background-color: var(--base-color);
}

input[type=text],
input[type=email] {
  height: 2.75rem;
}

textarea {
  height: 4.25rem;
}

@media (min-width: 992px) {
  .form-message {
    margin-right: 8.6875rem;
    width: 50%;
  }
}
.form-message__text {
  font-size: 0.9rem;
  line-height: 1.7;
  color: var(--base-color);
}
.form-message__text:not(:first-of-type) {
  margin-top: 1.875rem;
}